legend {
  overflow: hidden;
  display: inline-block;
  *display: inline;
  zoom: 1;
  text-indent: -9999px;
  white-space: nowrap;
  line-height: 0;
  width: 0;
  height: 0;
  font-size: 0;
}

.clearFix {
  clear: both;
}

.clearFix:after {
  content: " ";
  display: table;
}

.clearFix:after {
  clear: both;
}

.icon {
  position: relative;
  overflow: hidden;
  display: inline-block;
  *display: inline;
  zoom: 1;
  text-indent: -9999px;
  white-space: nowrap;
  line-height: 0;
  vertical-align: middle;
}

.txtHidden {
  overflow: hidden;
  display: inline-block;
  *display: inline;
  zoom: 1;
  text-indent: -9999px;
  white-space: nowrap;
  line-height: 0;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* reset */
body, div, dl, dt, dd, ul, ol, li, h1, h2, h3, h4, h5, h6, pre, code, form, fieldset, legend, textarea, p, blockquote, th, td, input, select, textarea, button {
  margin: 0;
  padding: 0;
}

fieldset, img {
  border: 0 none;
}

dl, ul, ol, menu, li {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: "";
  content: none;
}

input, select, textarea, button {
  vertical-align: middle;
}

button {
  border: 0 none;
  background-color: transparent;
  cursor: pointer;
}

body {
  background: #fff;
}

body, th, td, input, select, textarea, button {
  font-family: "Montserrat", "Pretendard", "NotoSans", Helvetica, "맑은 고딕", sans-serif;
} /* color값은 디자인가이드에 맞게사용 */
a {
  color: #333;
  text-decoration: none;
}

i {
  font-style: normal;
}

a:active, a:hover {
  text-decoration: none;
}

address, caption, cite, code, dfn, em, var {
  font-style: normal;
  font-weight: normal;
}

h1, h2, h3, h4, h5 {
  font-family: "Montserrat", "Pretendard", "NotoSans", Helvetica, "맑은 고딕", sans-serif;
  font-weight: 400;
}

input {
  -webkit-border-radius: 0;
  -webkit-appearance: none;
}

[tabindex]:focus, a:focus, textarea:focus, button:focus, input:focus {
  outline: none;
}

pre {
  font-size: 16px;
  font-family: "Montserrat", "Pretendard", "NotoSans", Helvetica, "맑은 고딕", sans-serif;
  font-weight: 400;
  white-space: normal;
}

body,
html {
  width: 100%;
  height: 100%;
  background-color: #111414;
  color: #efefef;
  margin: 0;
  padding: 0;
  list-style: none;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  background: inherit;
  border: none;
  box-shadow: none;
  border-radius: 0;
  overflow: visible;
  color: inherit;
  cursor: pointer;
}

.only-mobile {
  display: none;
}

.only-tablet {
  display: none;
}

.middot-list li {
  position: relative;
  padding-left: 0.8rem !important;
}

.middot-list li:before {
  display: block;
  position: absolute;
  top: 60%;
  left: 2px;
  width: 4px;
  height: 4px;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.4);
  transform: translateY(-50%);
  content: "";
}

.header {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  position: fixed;
  top: 0;
  width: 100%;
  height: 100px;
  padding: 20px 60px;
  animation: fadeIn 0.5s 2.8s forwards;
  transition: top 0.3s ease-in-out;
  transform: translateY(-100%);
  opacity: 1;
  z-index: 999;
}

.header .logo,
.header .menu-button {
  font-size: 1.125rem;
  font-weight: 800;
}

@keyframes fadeIn {
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
.header .logo {
  position: absolute;
  top: 50%;
  left: 60px;
  transform: translateY(-50%);
  transition: all 0.3s ease-in-out;
}

.header .logo.hide {
  top: -100%;
}

.header .menu-button {
  padding: 4px 12px;
  border: 2px solid #fbbf24;
  border-radius: 24px;
}

.header .nav {
  display: none;
  position: absolute;
  top: 74px;
  right: 0;
  text-align: center;
}

.header .nav .menu_list {
  display: flex;
  flex-direction: column;
  text-align: right;
}

.header .nav .menu_list .menu {
  opacity: 0;
  position: relative;
  right: -100px;
  font-size: 1.125rem;
  padding: 12px 40px;
  overflow: hidden;
}

.header .nav .menu_list .menu:hover {
  transform: rotate(26deg);
}

.header .nav .menu_list .menu:hover a {
  background-color: #c084fc;
}

.header .nav .menu_list .menu a {
  height: auto;
  padding: 4px 12px;
  font-weight: 600;
  border: 2px solid #fbbf24;
  background-color: #111414;
  border-radius: 24px;
}

.main,
footer {
  display: flex;
  align-items: center;
  flex-direction: column;
}

section {
  width: 1440px;
}

.main {
  gap: 160px;
  padding-top: 100px;
  overflow: hidden;
}

.main section .section-title {
  position: relative;
  padding: 32px 16px;
  font-size: 2rem;
  font-weight: 700;
  color: #fbbf24;
}

.main section .section-title sup {
  font-size: 0.875rem;
}

.main section .section-title:before {
  position: absolute;
  bottom: -1px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #fbbf24;
  transform: scaleX(0);
  transform-origin: left center;
  transition: transform 0.5s ease-out;
  content: "";
}

.main section .section-title.animate-border:before {
  transform: scaleX(1);
}

.main section.intro {
  position: relative;
  height: calc(100dvh - 100px);
}

.main section.intro .content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.main section.intro .content .main-title {
  position: absolute;
  font-size: 4.5rem;
  font-weight: 700;
  opacity: 0;
}

.main section.intro .content .main-title.from-top {
  top: 19%;
  transform: translateY(-200%) scale(3);
  animation: mainTitleTop 1.5s forwards;
}

.main section.intro .content .main-title.from-bottom {
  bottom: 30%;
  transform: translateY(200%) scale(3);
  animation: mainTitleBottom 1.5s forwards;
}

@keyframes mainTitleTop {
  65% {
    opacity: 1;
    transform: translateY(140px) scale(1);
  }
  75% {
    opacity: 1;
    transform: translateY(140px) scale(1);
  }
  100% {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}
@keyframes mainTitleBottom {
  65% {
    opacity: 1;
    transform: translateY(-140px) scale(1);
  }
  75% {
    opacity: 1;
    transform: translateY(-140px) scale(1);
  }
  100% {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}
.main section.intro .content .img-box {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  left: 50%;
  top: 45%;
  transform: translate(-50%, -50%);
  height: 0;
  width: 260px;
  border-radius: 24px;
  animation: introImgBox 0.8s 1.2s forwards ease-in-out;
  background-size: cover;
  overflow: hidden;
}

.main section.intro .content .img-box .intro-img {
  max-width: 260px;
  max-height: 260px;
  min-width: 220px;
  min-height: 220px;
  background: url(../images/intro.png) center center no-repeat;
  background-size: cover;
  border-radius: 24px;
}

@keyframes introImgBox {
  100% {
    height: 260px;
  }
}
.main section.intro .content .sub-title {
  position: absolute;
  font-size: 1.125rem;
  font-weight: 400;
  color: hsla(0, 0%, 100%, 0.6);
  text-align: center;
  opacity: 1;
}

.main section.intro .content .sub-title.first {
  bottom: 22%;
  animation: fadeIn 1s 2s forwards;
  opacity: 0;
  transform: translateY(20px);
}

.main section.intro .content .sub-title.second {
  opacity: 0;
  bottom: 19%;
  animation: fadeIn 1s 2.3s forwards;
  transform: translateY(20px);
}

.main section.works .works-list .work {
  padding: 40px 16px;
  border-bottom: 1px solid rgba(200, 200, 200, 0.2);
}

.main section.works .works-list .work .info .customer {
  font-size: 1.125rem;
  color: rgba(255, 255, 255, 0.6);
}

.main section.works .works-list .work .info .title {
  font-size: 3.5rem;
  font-weight: 500;
}

.main section.works .works-list .work .desc {
  display: flex;
  justify-content: space-between;
  padding-top: 24px;
}

.main section.works .works-list .work .desc .keywords {
  font-size: 1.125rem;
}

.main section.works .works-list .work .desc .keywords .kinds {
  color: rgba(255, 255, 255, 0.6);
  font-weight: 400;
}

.main section.works .works-list .work .desc .keywords .kinds img {
  height: 28px;
  padding-bottom: 4px;
  vertical-align: middle;
}

.main section.works .works-list .work .desc .keywords .kinds:last-of-type {
  position: relative;
  padding-left: 20px;
  border: none;
}

.main section.works .works-list .work .desc .keywords .kinds:last-of-type::before {
  position: absolute;
  left: 6px;
  top: 50%;
  font-size: 1rem;
  transform: translateY(-50%);
  content: "|";
}

.main section.works .works-list .work .desc .period {
  font-size: 1.25rem;
  font-weight: 400;
  color: rgba(239, 239, 239, 0.6823529412);
}

.main section.education .flex-wrap {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  gap: 80px;
  padding: 60px 16px;
}

.main section.education .flex-wrap .group.g1 {
  flex-basis: auto;
}

.main section.education .flex-wrap .group.g1 .edu-title {
  font-size: 3.8rem;
  font-weight: 700;
  line-height: 1.5;
  margin-bottom: 30px;
}

.main section.education .flex-wrap .group.g2 {
  display: flex;
  flex-direction: column;
  gap: 24px;
  flex-basis: 60%;
}

.main section.education .flex-wrap .group.g2 h4 {
  font-size: 1.25rem;
  font-weight: 500;
  color: rgba(255, 255, 255, 0.8);
}

.main section.education .flex-wrap .group.g2 .item {
  display: flex;
  flex-direction: column;
  gap: 16px;
  padding: 32px 0;
}

.main section.education .flex-wrap .group.g2 .item .text-list {
  display: flex;
  flex-direction: column;
  gap: 8px;
  text-align: left;
  font-size: 1.4rem;
  font-weight: 400;
}

.main section.education .flex-wrap .group.g2 .item .text-list .text {
  display: flex;
  align-items: flex-start;
}

.main section.education .flex-wrap .group.g2 .item .text-list .text .period {
  flex-basis: 260px;
  color: rgba(255, 255, 255, 0.6);
}

.main section.education .flex-wrap .group.g2 .item .text-list .text .label {
  font-weight: 500;
  word-break: keep-all;
}

.main section.education .flex-wrap .group.g2 .item .text-list .text:has(.academy):last-of-type {
  margin-top: 20px;
}

.main section.education .flex-wrap .group.g2 .item .text-list .text .academy ul {
  font-size: 0.875rem;
  color: rgba(255, 255, 255, 0.6);
}

.main section.education .flex-wrap .group.g2 .item .text-list .text .academy ul li {
  padding: 2px 0;
}

.main section.education .flex-wrap .group.g2 .item .text-list .text .academy ul li:first-of-type {
  padding-top: 8px;
}

.main section.education .flex-wrap .group.g2 .item + .item {
  border-top: 1px solid hsla(0, 0%, 78%, 0.2);
}

.main section.skills .skill-list {
  display: flex;
  flex-wrap: nowrap;
  gap: 60px;
  animation: scroll 2s linear infinite alternate-reverse;
  padding: 80px 0 0 60px;
  margin: 0 -36%;
  overflow: hidden;
}

@keyframes scroll {
  0% {
    transform: translateX(14%);
  }
  100% {
    transform: translateX(-14%);
  }
}
.main section.skills .skill-list .skill {
  max-width: 200px;
}

.main section.skills .skill-list .skill .img {
  width: 160px;
  vertical-align: top;
}

.main section.contact .content {
  position: relative;
}

.main section.contact .content .image-wrap {
  display: none; /*임시*/
  position: absolute;
  top: 0;
  width: 6em;
  height: 6em;
}

.main section.contact .content .image-wrap::after {
  position: absolute;
  top: 0;
  right: 0;
  width: 1.2em;
  height: 1.2em;
  background-color: #c084fc;
  border: 0.125em solid #efefef;
  border-radius: 50%;
  z-index: 90;
  content: "";
}

.main section.contact .content .image-wrap img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  border-radius: 50%;
}

.main section.contact .content .title {
  font-size: 3.8em;
  font-weight: 700;
  max-width: 60%;
  word-break: keep-all;
  padding: 0 16px 24px;
}

.main section.contact .content .phone,
.main section.contact .content .mail {
  font-size: 2rem;
  font-weight: 500;
  color: #c084fc;
  padding: 16px;
}

.footer {
  padding: 60px 0;
}

.footer .footer_copyright {
  position: relative;
  padding-top: 30px;
  color: rgba(255, 255, 255, 0.5);
}

.footer .footer_copyright:after {
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 1px;
  background: transparent;
  border-bottom: 1px solid #c084fc;
  transition: all 0.5s ease-out;
  content: "";
}

.footer .footer_animate:after {
  width: 100%;
}

/* desktop 규격 */
@media screen and (min-width: 1921px) {
  section {
    width: 1600px;
  }
  .main section.intro .content .main-title.from-top {
    top: 24%;
  }
  .main section.intro .content .main-title.from-bottom {
    bottom: 34%;
  }
  .main section.intro .content .sub-title {
    font-size: 1.75rem;
  }
  .main section.intro .content .sub-title.first {
    bottom: 22%;
  }
  .main section.intro .content .sub-title.second {
    bottom: 18%;
  }
  .main section.intro .content .img-box .intro-img {
    min-width: 260px;
    min-height: 260px;
  }
  .main section.skills .skill-list .skill .img {
    width: 180px;
  }
}
/* tablet 규격 */
/* mobile 규격 */
@media only screen and (max-width: 767px) {
  .only-mobile {
    display: block;
  }
  .header {
    height: 80px;
    padding: 24px;
  }
  .header .logo,
  .header .menu-button {
    font-size: 1rem;
  }
  .header .logo {
    left: 24px;
  }
  .header .menu-button {
    padding: 8px 12px;
  }
  .header .nav {
    top: 74px;
    right: 0;
  }
  .header .nav .menu_list {
